import React, { useEffect, useState } from 'react';
import Main from '../components/section/Main';
import { useSelector } from 'react-redux';
import { getCurrentTime, getDayType } from '../data/time';



const Nav = () => {
    const startStation = useSelector(state => state.startStation);
    const endStation = useSelector(state => state.endStation);

    const [currentTime, setCurrentTime] = useState(getCurrentTime());
    const [dayType, setDayType] = useState(getDayType());
    const [routeData, setRouteData] = useState([]);

    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(getCurrentTime());
        }, 1000);

        return () => clearInterval(timer);
    }, []);

    const yellowSegmentWidth = 30;
    const greenSegmentWidth = 60;
    const totalWidth = yellowSegmentWidth + greenSegmentWidth;

    const startPoint = { x: 0, y: 10 };
    const endPoint = { x: totalWidth, y: 10 };

    const fetchDataFromSpring = async () => {
        try {
            const url = `http://localhost:8080/SearchRoute?start=${encodeURIComponent(startStation)}&end=${encodeURIComponent(endStation)}`;
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
            });

            const data = await response.json();
            setRouteData(data);
        } catch (error) {
            console.error('데이터 가져오기 실패:', error);
        }
    };

    useEffect(() => {
        if (startStation && endStation) {
            fetchDataFromSpring();
        }
    }, [startStation, endStation]);

    const reconstructedData = routeData.map((route, index) => {
        const transferInfo = route.transferStations.map((transfer, tIndex) => {
            const trans = route.transferStationList[tIndex];
            return {
                station: transfer.station,
                transferTime: `${Math.floor(transfer.transferTime / 60)}분 ${transfer.transferTime % 60}초`,
                nextStation: transfer.nextStation,
                fromLine: transfer.fromLine,
                toLine: transfer.toLine,
                transferStationList: trans ? {
                    names: trans.name,
                    times: trans.transferTime.map(time => `${Math.floor(time / 60)}분 ${time % 60}초`),
                    scheduleTimes: trans.scheduleTime
                } : null
            };
        });

        return {
            routeNumber: index + 1,
            originalTime: route.originalTime,
            currentTime: route.currentTime,
            path: route.path.join(' -> '),
            transferInfo,
            finalTime: route.finalTime
        };
    });

    return (
        <Main title="실시간 길찾기" description="실시간 길찾기 페이지">
            <div className="resultsContainer">
                <h1 className="resultsHeader">길찾기 결과</h1>
                {startStation ? (
                    <p className="resultItem">출발지: {startStation}</p>
                ) : (
                    <p className="resultItem">출발지 정보가 없습니다.</p>
                )}
                {endStation ? (
                    <p className="resultItem">도착지: {endStation}</p>
                ) : (
                    <p className="resultItem">도착지 정보가 없습니다.</p>
                )}
            </div>
            <div className="resultsMap">
                <h1 className="resultsHeader">추천 경로</h1>
                <h3>예상 시간</h3>
                <svg width={totalWidth} height="20">
                    <rect x="0" y="0" width={yellowSegmentWidth} height="20" fill="yellow" />
                    <rect x={yellowSegmentWidth} y="0" width={greenSegmentWidth} height="20" fill="green" />
                    <circle cx={startPoint.x} cy={startPoint.y} r="5" fill="blue" />
                    <circle cx={endPoint.x} cy={endPoint.y} r="5" fill="red" />
                </svg>
            </div>
            <div className="currentTimeContainer">
                <p className="currentTime">현재 시간: {currentTime}</p>
                <p className="dayType">오늘은 {dayType}입니다.</p>
            </div>
            <div>
                <h2>Route Information</h2>
                {reconstructedData.length > 0 ? (
                    <ul>
                        {reconstructedData.map((route, index) => (
                            <li key={index}>
                                <p>경로 {route.routeNumber}:</p>
                                <p>시간표 반영 전 걸리는 시간: {route.originalTime}</p>
                                <p>현재 시간: {route.currentTime}</p>
                                <p>경로: {route.path}</p>
                                <p>환승 정보:</p>
                                <ul>
                                    {route.transferInfo.map((transfer, tIndex) => (
                                        <li key={tIndex}>
                                            <p>환승역: {transfer.station}, 환승 시간: {transfer.transferTime}</p>
                                            <p>다음 역: {transfer.nextStation}</p>
                                            <p>출발 호선: {transfer.fromLine}, 도착 호선: {transfer.toLine}</p>
                                            {transfer.transferStationList && (
                                                <>
                                                    <p>환승역 목록:</p>
                                                    <ul>
                                                        <li>
                                                            <p>환승역:</p>
                                                            <ul>
                                                                {transfer.transferStationList.names.map((name, index) => (
                                                                    <li key={index}>
                                                                        <p>{name}</p>
                                                                        <p>환승역까지
                                                                            시간: {transfer.transferStationList.times[index]}</p>
                                                                        <p>탈 수 있는 시간표
                                                                            시간: {transfer.transferStationList.scheduleTimes[index]}</p>
                                                                    </li>
                                                                ))}
                                                            </ul>
                                                        </li>
                                                    </ul>
                                                </>
                                            )}
                                        </li>
                                    ))}
                                </ul>
                                <p>예상 최종 시간: {route.finalTime}</p>
                            </li>
                        ))}
                    </ul>
                ) : (
                    <p>경로 정보를 불러오지 못했습니다.</p>
                )}
            </div>
        </Main>
    );
};

export default Nav;